:root {
  --text--white: #fff;
  --hero-background: #f24c26;
}

header {
  text-align: center;
  z-index: 1000;
  width: 100%;
  padding: 20px;
  position: fixed;
  top: 0;
  left: 0;
}

.nav-component ul {
  justify-content: space-around;
  list-style-type: none;
  display: flex;
}

.nav-component ul li {
  padding: 10px;
  font-size: 1.2rem;
}

.nav-component ul li a {
  color: var(--text--white);
  text-decoration: none;
}

.nav-end {
  display: flex;
}

.hero-component {
  color: var(--text--white);
  background-color: var(--hero-background);
  text-align: center;
  justify-content: center;
  align-items: center;
  height: 100vh;
  display: flex;
  position: relative;
}

.hero-component h1 {
  z-index: 1;
  margin: 0;
  font-size: 3rem;
}

.slide-img {
  opacity: 0;
  width: 50px;
  transition: opacity .3s;
  position: absolute;
  bottom: -50px;
  left: 50%;
  transform: translateX(-50%)translateY(100px);
}

@media (width >= 768px) {
  .hero-component h1 {
    font-size: 7rem;
  }
}

.quote-component, .quote-component--purple {
  justify-content: center;
  align-items: left;
  color: var(--text--white);
  background-color: #1e01ad;
  flex-direction: column;
  height: 100vh;
  padding: 0 .5rem;
  display: flex;
}

.quote-component span, .quote-component--purple span {
  font-size: 2.7rem;
  font-weight: bold;
  line-height: 1;
}

.quote-component span:nth-child(2) {
  padding-left: 4rem;
}

.quote-component span:nth-child(3) {
  padding-left: 8rem;
}

@media (width >= 768px) {
  .quote-component {
    align-items: center;
  }

  .quote-component span {
    font-size: 9rem;
  }

  .quote-component span:nth-child(2) {
    padding-left: 8rem;
  }

  .quote-component span:nth-child(3) {
    padding-left: 12rem;
  }
}

.about-component {
  margin: 2rem 0;
}

.about-component__info {
  margin: 4rem 2rem;
  position: relative;
}

.about-component__info h2 {
  text-align: center;
}

.underline--full {
  background-color: #000;
  width: 30%;
  height: 3px;
  margin: auto;
}

.underline--half {
  background-color: var(--hero-background);
  z-index: 1000;
  width: 15%;
  height: 3px;
  margin: auto;
  position: absolute;
  top: 36px;
  left: 0;
  right: 0;
}

.about-component__info p {
  margin: 2rem 0;
}

@media (width >= 768px) {
  .about-component {
    justify-content: space-between;
    display: flex;
  }
}

.quote-component--purple {
  background-color: #6301ad;
}

.quote-component--purple span {
  color: var(--text--white);
}

.quote--right {
  text-align: right;
}

@media (width >= 768px) {
  .quote-component--purple span {
    font-size: 9rem;
  }

  .quote--right {
    justify-content: flex-end;
    gap: 2rem;
    display: flex;
  }

  .quote--second {
    gap: 1rem;
    display: flex;
  }
}

footer {
  background-color: #ffefef;
  padding: 1rem;
}

footer a {
  display: flex;
}

.footer-intro, .info--myself, .info--school {
  border-bottom: 1px solid #000;
  margin: 2rem 0;
  padding: 2rem 0;
}

.footer-intro a {
  color: #000;
  margin: .5rem 0;
  text-decoration: none;
}

.footer-intro h3, .info--myself h3, .info--school h3 {
  font-weight: bold;
}

a.email-link {
  cursor: pointer;
  color: #000;
  align-items: center;
  font-size: 18px;
  text-decoration: none;
  display: flex;
}

.arrow {
  transform-origin: 0;
  margin-left: 10px;
  font-size: 24px;
  transition: transform .3s;
  display: inline-block;
}

.footer__icon {
  width: 2rem;
  height: 2rem;
}

.info--myself p, .info--school p {
  font-style: normal;
}

@media (width >= 768px) {
  footer {
    justify-content: space-around;
    display: flex;
  }

  .footer-intro, .info--myself, .info--school {
    border: none;
  }
}

.footer-title {
  margin-bottom: 2rem;
  font-size: 1.5rem;
  font-weight: bold;
}

.circle {
  mix-blend-mode: exclusion;
  background-color: red;
  border-radius: 50%;
  width: 2rem;
  height: 2rem;
  position: fixed;
}
