:root {
  --text--white: #fff;
  --hero-background: #f24c26;
}

header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  padding: 20px;
  text-align: center;
  z-index: 1000; /* Ensure it stays on top */
}

.nav-component ul {
  display: flex;
  justify-content: space-around;
  list-style-type: none;
}

.nav-component ul li {
  padding: 10px;
  font-size: 1.2rem;
}

.nav-component ul li a {
  text-decoration: none;
  color: var(--text--white);
}

.nav-end {
  display: flex;
}

.hero-component {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  color: var(--text--white);
  background-color: var(--hero-background);
  position: relative;
  text-align: center;
}

.hero-component h1 {
  font-size: 3rem;
  margin: 0;
  z-index: 1;
}

.slide-img {
  position: absolute;
  bottom: -50px; /* Position it below the h1 initially */
  left: 50%;
  transform: translateX(-50%) translateY(100px); /* Initially hide it below */
  width: 50px; /* Adjust the icon size to your preference */
  opacity: 0; /* Initially hidden */
  transition: opacity 0.3s ease; /* Smooth fade in/out */
}


@media (min-width: 768px) {
  .hero-component h1 {
    font-size: 7rem;
  }
}

.quote-component,
.quote-component--purple {
  display: flex;
  justify-content: center;
  align-items: left;
  flex-direction: column;
  height: 100vh;
  color: var(--text--white);
  background-color: #1e01ad;
  padding: 0 0.5rem;
}

.quote-component span,
.quote-component--purple span {
  font-size: 2.7rem;
  font-weight: bold;
  line-height: 1;
}

.quote-component span:nth-child(2) {
  padding-left: 4rem;
}

.quote-component span:nth-child(3) {
  padding-left: 8rem;
}

@media (min-width: 768px) {
  .quote-component {
    align-items: center;
  }

  .quote-component span {
    font-size: 9rem;
  }

  .quote-component span:nth-child(2) {
    padding-left: 8rem;
  }

  .quote-component span:nth-child(3) {
    padding-left: 12rem;
  }
}

.about-component {
  margin: 2rem 0;
}

.about-component__info {
  position: relative;
  margin: 4rem 2rem;
}

.about-component__info h2 {
  text-align: center;
}

.underline--full {
  height: 3px;
  background-color: black;
  width: 30%;
  margin: auto;
}

.underline--half {
  height: 3px;
  background-color: var(--hero-background);
  width: 15%;
  margin: auto;
  position: absolute;
  left: 0;
  right: 0;
  top: 36;
  z-index: 1000;
}

.about-component__info p {
  margin: 2rem 0;
}

@media (min-width: 768px) {
  .about-component {
    display: flex;
    justify-content: space-between;
  }
}

.quote-component--purple {
  background-color: #6301ad;
}

.quote-component--purple span {
  color: var(--text--white);
}

.quote--right {
  text-align: right;
}

@media (min-width: 768px) {
  .quote-component--purple span {
    font-size: 9rem;
  }

  .quote--right {
    display: flex;
    justify-content: flex-end;
    gap: 2rem;
  }

  .quote--second{
    display: flex;
    gap: 1rem;
  }
}

footer {
  background-color: #ffefef;
  padding: 1rem;
}

footer a {
  display: flex;
}

.footer-intro,
.info--myself,
.info--school {
  margin: 2rem 0;
  padding: 2rem 0;
  border-bottom: black 1px solid;
}

.footer-intro a {
  margin: 0.5rem 0;
  text-decoration: none;
  color: #000;
}

.footer-intro h3,
.info--myself h3,
.info--school h3 {
  font-weight: bold;
}

a.email-link {
  cursor: pointer;
  text-decoration: none;
  color: black;
  font-size: 18px;
  display: flex;
  align-items: center;
}

.arrow {
  font-size: 24px;
  margin-left: 10px; /* Space between the email and the arrow */
  transform-origin: left center; /* Anchor the arrow to the left for scaling */
  display: inline-block;
  transition: transform 0.3s ease; /* Fallback if GSAP doesn't load */
}

.footer__icon {
  width: 2rem;
  height: 2rem;
}

.info--myself p,
.info--school p {
  font-style: normal;
}

@media (min-width: 768px) {
  footer {
    display: flex;
    justify-content: space-around;
  }
  .footer-intro,
  .info--myself,
  .info--school {
    border: none;
  }
}

.footer-title{
  font-size: 1.5rem;
  font-weight: bold;
  margin-bottom: 2rem;
}

.circle {
  width: 2rem;
  height: 2rem;
  border-radius: 50%;
  background-color: red;
  mix-blend-mode: exclusion;
  position: fixed;
}